import React, { Component } from 'react'
import { withFirebase } from '../Firebase/'
import { ReactComponent as Heart } from '../assets/heart-solid.svg'
import './List.css'

const TABS = [
    {
        title: 'Todo',
        db: 'todo',
        index: 0,
    },
    {
        title: 'Titta',
        db: 'watch',
        index: 1,
    },
    {
        title: 'Mat',
        db: 'food',
        index: 2
    }
]

class List extends Component {
    state = {
        data: {},
        selectedTab: TABS[0],
        inputValue: ''
    }

    componentDidMount() {
        this.props.firebase
            .doGetList(data => this.setState({ data }))
    }

    handleNewTodo = event => {
        event.preventDefault()
        const { inputValue, selectedTab } = this.state
        if (inputValue.length > 0) {
            this.setState({ inputValue: '' })
            this.props.firebase.doAddNewTodo(inputValue, selectedTab.db)
        }
    }

    handleRemoveTodo = index => {
        const { data, selectedTab } = this.state
        this.props.firebase.doRemoveTodo(data[selectedTab.db][index], selectedTab.db)
    }

    handleTabChange = index => {
        this.setState({ selectedTab: TABS[index] })
    }

    render() {
        const { data, inputValue, selectedTab } = this.state
        const todos = data[selectedTab.db] ? data[selectedTab.db] : []
        return (
            <div className="list-container">
                <h1>Saker Att GÃ¶ra Medan Man Pussas</h1>
                <form onSubmit={this.handleNewTodo}>
                    <input
                        type="text"
                        value={inputValue}
                        onChange={e =>
                            this.setState({ inputValue: e.target.value })
                        }
                    />
                    <button onClick={this.handleNewTodo}>
                        <Heart />
                    </button>
                </form>
                <div className="tabs">
                    {TABS.map((tab, index) => (
                        <div
                            key={index}
                            className={
                                this.state.selectedTab.index === index
                                    ? 'tab active'
                                    : 'tab'
                            }
                        >
                            <button onClick={() => this.handleTabChange(index)}>
                                {tab.title}
                            </button>
                        </div>
                    ))}
                </div>
                <div className="list">
                    <ul>
                        {todos.map((todo, index) => (
                            <li key={index}>
                                <span>{todo}</span>
                                <button
                                    onClick={() => this.handleRemoveTodo(index)}
                                />
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        )
    }
}

export default withFirebase(List)
